.tippy-tooltip {
  position: relative;
  color: white;
  border-radius: 4px;
  font-size: 0.9rem;
  padding: 0.3rem 0.6rem;
  max-width: 350px;
  text-align: center;
  will-change: transform;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333; }
  .tippy-tooltip[data-size='small'] {
    padding: 0.2rem 0.4rem;
    font-size: 0.75rem; }
  .tippy-tooltip[data-size='large'] {
    padding: 0.4rem 0.8rem;
    font-size: 1rem; }
  .tippy-tooltip[data-animatefill] {
    overflow: hidden;
    background-color: transparent; }
    .tippy-tooltip[data-animatefill] .tippy-content {
      transition: clip-path cubic-bezier(0.46, 0.1, 0.52, 0.98); }
  .tippy-tooltip[data-interactive] {
    pointer-events: auto; }
    .tippy-tooltip[data-interactive] path {
      pointer-events: auto; }
  .tippy-tooltip[data-inertia][data-state='visible'] {
    transition-timing-function: cubic-bezier(0.53, 2, 0.36, 0.85); }
  .tippy-tooltip[data-inertia][data-state='hidden'] {
    transition-timing-function: ease; }

.tippy-arrow,
.tippy-roundarrow {
  position: absolute;
  width: 0;
  height: 0; }

.tippy-roundarrow {
  width: 24px;
  height: 8px;
  fill: #333;
  pointer-events: none; }

.tippy-backdrop {
  position: absolute;
  will-change: transform;
  background-color: #333;
  border-radius: 50%;
  width: 26%;
  left: 50%;
  top: 50%;
  z-index: -1;
  transition: all cubic-bezier(0.46, 0.1, 0.52, 0.98);
  backface-visibility: hidden; }
  .tippy-backdrop::after {
    content: '';
    float: left;
    padding-top: 100%; }

/* iOS Safari does not like clip path transition */
body:not(.tippy-iOS) .tippy-tooltip[data-animatefill][data-state='visible'] .tippy-content {
  clip-path: ellipse(100% 100% at 50% 50%); }

body:not(.tippy-iOS) .tippy-tooltip[data-animatefill][data-state='hidden'] .tippy-content {
  clip-path: ellipse(5% 50% at 50% 50%); }

body:not(.tippy-iOS) .tippy-popper[x-placement='right'] .tippy-tooltip[data-animatefill][data-state='visible'] .tippy-content {
  clip-path: ellipse(135% 100% at 0% 50%); }

body:not(.tippy-iOS) .tippy-popper[x-placement='right'] .tippy-tooltip[data-animatefill][data-state='hidden'] .tippy-content {
  clip-path: ellipse(40% 100% at 0% 50%); }

body:not(.tippy-iOS) .tippy-popper[x-placement='left'] .tippy-tooltip[data-animatefill][data-state='visible'] .tippy-content {
  clip-path: ellipse(135% 100% at 100% 50%); }

body:not(.tippy-iOS) .tippy-popper[x-placement='left'] .tippy-tooltip[data-animatefill][data-state='hidden'] .tippy-content {
  clip-path: ellipse(40% 100% at 100% 50%); }

@media (max-width: 360px) {
  .tippy-popper {
    max-width: 96%;
